import { initializeApp } from "firebase/app";
import {
    getStorage,
    ref,
    uploadBytes,
    getDownloadURL,
    deleteObject,
} from "firebase/storage";

export const app = initializeApp({
    projectId: "socialmedia-8c50b",
    appId: "1:659710868243:web:701707ae22e534833738e6",
    storageBucket: "socialmedia-8c50b.appspot.com",
    locationId: "asia-south1",
    apiKey: "AIzaSyBKhyqduWabwS1rqkLz069YIYCkuJWsNW8",
    authDomain: "socialmedia-8c50b.firebaseapp.com",
    messagingSenderId: "659710868243",
    measurementId: "G-37467H01J1",
});

const storage = getStorage(app);

export const upload = (file, filename) => {
    // Create a storage reference from our  storage service
    // uploading
    console.log("uploading ");
    if (
        filename.endsWith(".jpg") ||
        filename.endsWith(".png") ||
        filename.endsWith(".jpeg")
    ) {
        const storageRef = ref(storage, "images/" + filename);
        return uploadBytes(storageRef, file).then(() => {
            return getDownloadURL(ref(storage, "images/" + filename)).then(
                (url) => {
                    console.log(url);
                    return url;
                }
            );
        });
    } else if (filename.endsWith(".mp4")) {
        const storageRef = ref(storage, "videos/" + filename);
        return uploadBytes(storageRef, file).then(() => {
            return getDownloadURL(ref(storage, "videos/" + filename)).then(
                (url) => {
                    console.log(url);
                    return url;
                }
            );
        });
    }
};

export const deleteFile = (filename, fileType) => {
    console.log(filename,fileType)
    const fileRef = ref(storage, fileType + "s/" + filename);
    console.log(fileRef)
    return deleteObject(fileRef);
};
